import Vue from 'vue'
import App from './App'
import router from './router'
import $ from 'jquery'

import './assets/css/bootstrasp-cosme.css'  
import './assets/js/bootstrap.min.js'  
import './assets/css/style.css'
import './assets/js/dom-to-image.js'
Vue.config.productionTip = false

/* eslint-disable no-new */

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})
document.getElementById('j_begin').addEventListener('click',()=>{
  
  // let width = document.getElementById('my-node').getBoundingClientRect().width;let height = 
  let width = document.body.clientWidth
  let height = document.getElementById('my-node').getBoundingClientRect().height;
console.log(width,height)
  domtoimage.toPng(document.getElementById('my-node'), { quality: 1,bgcolor:'#fff',width:width,height:height+15})
  .then(function (dataUrl) {
      var link = document.createElement('img');
      // link.download = 'my-image-name.jpeg';
      link.src = dataUrl;
      document.getElementById('test').appendChild(link)

      // link.click();
  });



  
},false)


